import { Field, ID, ObjectType } from "@nestjs/graphql";
import { Column, CreateDateColumn, DeleteDateColumn, PrimaryColumn, UpdateDateColumn } from "typeorm";
import { v4 } from 'uuid';

@ObjectType()
export class BaseEntityColumn {

  @Field(() => ID)
  @PrimaryColumn()
  id: string = v4();

  @Field(() => Boolean)
  @Column({ default: false })
  isPublished!: boolean;

  @Field(() => Boolean)
  @Column({ default: true })
  isActive!: boolean;

  @Field()
  @CreateDateColumn()
  createdAt: Date = new Date();

  @Field({ nullable: true })
  @UpdateDateColumn()
  updatedAt?: Date;

  @Field({ nullable: true })
  @DeleteDateColumn()
  deletedAt?: Date;

}